import React, { useState, useEffect } from 'react';
import callCookie from '../../utils/cookie';
import history from '../../utils/browserHistory';

import { login } from '../../container/sign';

import {
  LoginPage,
  StaySignedInBox,
  StaySignedInBtn,
  StaySignedInLabel,
  LinkBox,
  FindLink,
  LoginBtn,
  SignupLink,
} from './style';

import { Input } from '../common';

function Login() {
  const [isStaySignedIn, setIsStaySignedIn] = useState(false);
  const [loginField, setLoginField] = useState({
    email: '',
    password: '',
  });

  const { email, password } = loginField;

  const changeState = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;

    setLoginField({
      ...loginField,
      [name]: value,
    });
  };

  const onClickLoginBtn = async function (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>,
  ) {
    const target = e.currentTarget;
    target.disabled = true;
    target.classList.add('on');

    const res = await login(isStaySignedIn, { id: email, password });
    setLoginField({
      email: '',
      password: '',
    });
    target.disabled = false;
    target.classList.remove('on');

    if (res?.success === false) {
      alert(res.msg);
      return;
    }

    history.push('/main');
  };

  useEffect(() => {
    callCookie.delete('staySignedIn');
    callCookie.delete('jwt');
  }, []);

  return (
    <LoginPage>
      <Input
        name="email"
        placeholder="ì´ë©”ì¼"
        value={email}
        onChange={changeState}
      />
      <Input
        name="password"
        placeholder="ë¹„ë°€ë²ˆí˜¸"
        type="password"
        value={password}
        onChange={changeState}
      />

      <LoginBtn type="button" onClick={onClickLoginBtn}>
        ë¡œê·¸ì¸
      </LoginBtn>

      <SignupLink type="button" onClick={() => history.push('/signup')}>
        íšŒì›ê°€ì…
      </SignupLink>

      <LinkBox>
        <StaySignedInBox
          onClick={() => {
            setIsStaySignedIn(!isStaySignedIn);
          }}
        >
          <StaySignedInBtn type="button" className={isStaySignedIn ? 'on' : ''}>
            âœ”
          </StaySignedInBtn>
          <StaySignedInLabel>ë¡œê·¸ì¸ ìƒíƒœ ìœ ì§€</StaySignedInLabel>
        </StaySignedInBox>

        <FindLink onClick={() => history.push('/find')}>ë¹„ë°€ë²ˆí˜¸ ì°¾ê¸°</FindLink>
      </LinkBox>
    </LoginPage>
  );
}

export default Login;
